import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { REFERENCE, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../../dependencies/empresaMCGDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, LalurLacsParteBRelRV, REPORT_EVENT, REPORT_TITLE } from './lalurLacsParteBRelRV';

const DEFAULT_VALUES: LalurLacsParteBRelRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  lacs: true,
  lalur: true,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, REFERENCE.value),
  exibirConfiguracaoDadosEmpresa: true,
  refInicialLabel: 'referência',
  refInicialRequired: true,
};

const LalurLacsParteBReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<LalurLacsParteBRelRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const carregarParametrosEmpresa = () => {
    formProps.getValues().empresaMCG = globalReducer.globalParameter.empresaMCG;
  };

  const validarCheckbox = () => {
    const erros = [];

    if (!formProps.getValues().lalur && !formProps.getValues().lacs) {
      erros.push('Pelo menos 1 (um) tipo de livro de apuração deve ser selecionado.');
    }

    return erros;
  };

  const init = () => {
    setTimeout(() => {
      carregarParametrosEmpresa();
      formProps.setValue('referenciaInicial', globalReducer.globalParameter.empresaMCG.refAtual);
    }, 1000);
  };

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa) {
        init();
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent
      title="Relatório Parte A"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
    >
      <Row>
        <Col>
          <CustomInput type="switch" label="Lalur" name="lalur" id="lalur" innerRef={formProps.register} />
        </Col>
        <Col>
          <CustomInput type="switch" label="Lacs" name="lacs" id="lacs" innerRef={formProps.register} />
        </Col>
      </Row>
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validarCheckbox}
      />
    </WindowComponent>
  );
};

export default LalurLacsParteBReportView;
